"use client";
import { useState, useEffect } from "react";
import "./globals.css";
import { FiMoon } from "react-icons/fi";
import { IoIosSearch } from "react-icons/io";
import { FiMapPin } from "react-icons/fi";
import { BsCloudRain } from "react-icons/bs";
import { BsCloudSnow } from "react-icons/bs";
import { AiOutlineThunderbolt } from "react-icons/ai";
import { RiDrizzleLine } from "react-icons/ri";
import { CiCloud } from "react-icons/ci";
import { AiOutlineSun } from "react-icons/ai";
import { FaWind } from "react-icons/fa";
import { CiDroplet } from "react-icons/ci";
import { FiEye } from "react-icons/fi";
import { FaGaugeSimple } from "react-icons/fa6";
import { FiSun } from "react-icons/fi";
import styled from "styled-components";
import axios from "axios";

export interface WeatherResponse {
  location: WeatherLocation;
  current: CurrentWeather;
}

export interface WeatherLocation {
  name: string;
  region: string;
  country: string;
  lat: number;
  lon: number;
  tz_id: string;
  localtime_epoch: number;
  localtime: string;
}

export interface CurrentWeather {
  last_updated_epoch: number;
  last_updated: string;
  temp_c: number;
  temp_f: number;
  is_day: number;
  condition: WeatherCondition;
  wind_mph: number;
  wind_kph: number;
  wind_degree: number;
  wind_dir: string;
  pressure_mb: number;
  pressure_in: number;
  precip_mm: number;
  precip_in: number;
  humidity: number;
  cloud: number;
  feelslike_c: number;
  feelslike_f: number;
  windchill_c: number;
  windchill_f: number;
  heatindex_c: number;
  heatindex_f: number;
  dewpoint_c: number;
  dewpoint_f: number;
  vis_km: number;
  vis_miles: number;
  uv: number;
  gust_mph: number;
  gust_kph: number;

  // Optional fields (not always present)
  short_rad?: number;
  diff_rad?: number;
  dni?: number;
  gti?: number;
}

export interface WeatherCondition {
  text: string;
  icon: string;
  code: number;
}

export interface ForecastResponse {
  forecastday: ForecastDay[];
}

export interface ForecastDay {
  date: string;
  date_epoch: number;
  day: DayInfo;
  astro: AstroInfo;
  hour: HourInfo[];
}

export interface DayInfo {
  maxtemp_c: number;
  maxtemp_f: number;
  mintemp_c: number;
  mintemp_f: number;
  avgtemp_c: number;
  avgtemp_f: number;
  maxwind_mph: number;
  maxwind_kph: number;
  totalprecip_mm: number;
  totalprecip_in: number;
  totalsnow_cm: number;
  avgvis_km: number;
  avgvis_miles: number;
  avghumidity: number;
  daily_will_it_rain: number;
  daily_chance_of_rain: number;
  daily_will_it_snow: number;
  daily_chance_of_snow: number;
  condition: Condition;
  uv: number;
}

export interface AstroInfo {
  sunrise: string;
  sunset: string;
  moonrise: string;
  moonset: string;
  moon_phase: string;
  moon_illumination: number;
  is_moon_up: number;
  is_sun_up: number;
}

export interface HourInfo {
  time_epoch: number;
  time: string;

  temp_c: number;
  temp_f: number;

  is_day: number;

  condition: Condition;

  wind_mph: number;
  wind_kph: number;
  wind_degree: number;
  wind_dir: string;

  pressure_mb: number;
  pressure_in: number;

  precip_mm: number;
  precip_in: number;

  snow_cm: number;

  humidity: number;
  cloud: number;

  feelslike_c: number;
  feelslike_f: number;

  windchill_c: number;
  windchill_f: number;

  heatindex_c: number;
  heatindex_f: number;

  dewpoint_c: number;
  dewpoint_f: number;

  will_it_rain: number;
  chance_of_rain: number;
  will_it_snow: number;
  chance_of_snow: number;

  vis_km: number;
  vis_miles: number;

  gust_mph: number;
  gust_kph: number;

  uv: number;

  short_rad: number;
  diff_rad: number;
  dni: number;
  gti: number;
}

export interface Condition {
  text: string;
  icon: string;
  code: number;
}

const Form = styled.form`
  display: flex;
  border: 1px solid white;
  border-radius: 5px;
  overflow: hidden;
  color: black;
  background-color: white;
  width: 47%;
`;

const Input = styled.input`
  flex: 1;
  margin: 10px;
  border: none;
  outline: none;
  font-size: 1rem;
  &::placeholder {
    color: black;
  }
`;

const Button = styled.button`
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background: transparent;
  cursor: pointer;
`;

const Page = () => {
  const [searchQuery, setSearchQuery] = useState("");
  const [suggestions, setSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [recentSearches, setRecentSearches] = useState([]);
  const [currentWeather, setCurrentWeather] = useState<WeatherResponse | null>(
    null
  );
  const [forecast, setForecast] = useState<ForecastResponse | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    const saved = localStorage.getItem("recentSearches");
    if (saved) {
      setRecentSearches(JSON.parse(saved));
    }
    const savedTheme = localStorage.getItem("darkMode");
    if (savedTheme) {
      setDarkMode(JSON.parse(savedTheme));
    }
  }, []);

  const toggleDarkMode = () => {
    const newMode = !darkMode;
    setDarkMode(newMode);
    localStorage.setItem("darkMode", JSON.stringify(newMode));
  };

  const getWeatherIcon = (condition) => {
    const code = condition?.toLowerCase() || "";
    if (code.includes("rain")) return <BsCloudRain size={26} />;
    if (code.includes("snow")) return <BsCloudSnow size={26} />;
    if (code.includes("drizzle")) return <RiDrizzleLine size={26} />;
    if (code.includes("thunder")) return <AiOutlineThunderbolt size={26} />;
    if (code.includes("partlycloudy")) return <CiCloud size={26} />;
    return <AiOutlineSun size={46} />;
  };

  const handleInputChange = async (e) => {
    const value = e.target.value;
    setSearchQuery(value);

    if (value.length > 2) {
      try {
        const baseUrl = process.env.NEXT_PUBLIC_BASEURL;
        const params = {
          q: searchQuery,
        };
        const res = await axios.get(`${baseUrl}/api/locations/autocomplete`, {
          params,
        });
        setSuggestions(res.data);
        setShowSuggestions(true);
      } catch (err) {
        console.log(err);
      }
    } else {
      setShowSuggestions(false);
    }
  };

  const handleSearch = async (location) => {
    setShowSuggestions(false);
    setLoading(true);
    setError(null);

    try {
      const baseUrl = process.env.NEXT_PUBLIC_BASEURL;
      const params = {
        q: location.name,
      };

      const weather = await axios.get(`${baseUrl}/api/weather/current`, {
        params,
      });

      const forecastParams = {
        q: location.name,
        days: 5,
      };

      const forecastResult = await axios.get(
        `${baseUrl}/api/weather/forecast`,
        { params: forecastParams }
      );

      setCurrentWeather(weather.data);
      setForecast(forecastResult.data.forecast);

      const updated = [
        location,
        ...recentSearches.filter((s) => s.name !== location.name),
      ];

      setRecentSearches(updated);
      localStorage.setItem("recentSearches", JSON.stringify(updated));
    } catch (err) {
      setError("Failed to fetch weather data");
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      {/* Header */}
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-evenly",
        }}
      >
        <div>
          <h1>Weather</h1>
          <p style={{ color: `${darkMode ? "#9CA3AF" : "#4B5563"}` }}>
            Search for a city to view current weather conditions
          </p>
        </div>
        <div>
          <button
            style={{
              borderRadius: "50%",
              border: "1px",
              borderColor: "white",
              cursor: "pointer",
            }}
            onClick={toggleDarkMode}
          >
            {darkMode ? <FiSun size={24} /> : <FiMoon size={24} />}
          </button>
        </div>
      </div>

      {/* Search bar*/}
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          marginTop: "1rem",
          flexDirection: "column",
          gap: "1rem",
        }}
      >
        <Form>
          <Input
            id="title"
            name="title"
            placeholder="Search for a location..."
            value={searchQuery}
            onChange={handleInputChange}
          />
          <Button type="submit" aria-label="Search">
            <IoIosSearch size={26} />
          </Button>
        </Form>

        {showSuggestions && suggestions.length > 0 && (
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              width: "47%",
              marginTop: "-0.5rem",
            }}
          >
            {suggestions.map((sug) => (
              <button
                style={{
                  borderColor: "white",
                  border: "1px",
                  textAlign: "start",
                  height: "2rem",
                  cursor: "pointer",
                }}
                key={sug.id}
                onClick={() => {
                  setSearchQuery(sug.name);
                  handleSearch(sug);
                }}
              >
                <div>
                  <FiMapPin />
                  <span>{sug.name}</span>
                </div>
              </button>
            ))}
          </div>
        )}
      </div>

      {/* Recent Searches */}
      {recentSearches.length > 0 && !currentWeather && (
        <div style={{ display: "flex", justifyContent: "center" }}>
          <div style={{ width: "47%" }}>
            <h3>Recent Searches</h3>
            <div style={{ display: "flex" }}>
              {recentSearches.map((loc) => (
                <button key={loc.id} onClick={() => handleSearch(loc)}>
                  {loc.name}
                  {", "}
                  {loc.region}
                </button>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Error display */}
      {error && <div>{error}</div>}

      {currentWeather && !loading && (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            width: "47%",
          }}
        >
          {/* Current Weather Card */}
          <div
            style={{
              background: "white",
              margin: "2rem",
              padding: "1rem",
              width: "47%",
              borderRadius: "12px",
            }}
          >
            <div
              style={{
                display: "flex",
                alignItems: "start",
                justifyContent: "space-between",
                padding: "1rem",
              }}
            >
              <div>
                <h2
                  style={{ margin: "0" }}
                >{`${currentWeather.location.name}, ${currentWeather.location.country}`}</h2>
                <p style={{ marginTop: "0" }}>
                  {currentWeather.current.condition.text}
                </p>
              </div>
              <div>{getWeatherIcon(currentWeather.current.condition.text)}</div>
            </div>

            <div style={{ paddingInline: "1rem" }}>
              <div style={{ fontSize: "40px" }}>
                {currentWeather.current.temp_c}째
              </div>
              <p
                style={{ marginTop: "0", fontSize: "14px", fontWeight: "100" }}
              >
                Feels like {currentWeather.current.feelslike_c}째
              </p>
            </div>

            {/* Weather Details Grid */}
            <div
              style={{
                marginTop: "1rem",
                padding: "1rem",
                display: "grid",
                gridTemplateColumns: "1fr 1fr",
              }}
            >
              <div style={{ display: "flex", alignItems: "center" }}>
                <FaWind />
                <div style={{ marginInlineStart: "1rem" }}>
                  <p style={{ marginBottom: "0" }}>Wind</p>
                  <p style={{ marginTop: "5px" }}>
                    {currentWeather.current.wind_kph} km/h
                  </p>
                </div>
              </div>
              <div style={{ display: "flex", alignItems: "center" }}>
                <CiDroplet />
                <div style={{ marginInlineStart: "1rem" }}>
                  <p style={{ marginBottom: "0" }}>Humidity</p>
                  <p style={{ marginTop: "5px" }}>
                    {currentWeather.current.humidity}%
                  </p>
                </div>
              </div>
              <div style={{ display: "flex", alignItems: "center" }}>
                <FiEye />
                <div style={{ marginInlineStart: "1rem" }}>
                  <p style={{ marginBottom: "0" }}>Visibility</p>
                  <p style={{ marginTop: "5px" }}>
                    {currentWeather.current.vis_km} km
                  </p>
                </div>
              </div>
              <div style={{ display: "flex", alignItems: "center" }}>
                <FaGaugeSimple />
                <div style={{ marginInlineStart: "1rem" }}>
                  <p style={{ marginBottom: "0" }}>Pressure</p>
                  <p style={{ marginTop: "5px" }}>
                    {currentWeather.current.pressure_mb} mb
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* 5-Day Forecast */}
          <div
            style={{
              background: "white",
              width: "47%",
              padding: "1rem",
              borderRadius: "12px",
            }}
          >
            <h3>5-Day Forecast</h3>
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr",
                gap: "2rem",
              }}
            >
              {forecast.forecastday.map((day, idx) => (
                <div key={idx}>
                  <p>{day.date}</p>
                  <div>{getWeatherIcon(day.day.condition.text)}</div>
                  <div>
                    <p>{day.day.maxtemp_c}째</p>
                    <p>{day.day.mintemp_c}째</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Page;
